import { trigger, AnimationEntryMetadata } from '@angular/core';

import { fade } from './animations/fade';
import { bounce } from './animations/bounce';
import { rotate } from './animations/rotate';
import { slide } from './animations/slide';
import { zoom } from './animations/zoom';

export const defaults = {
    duration:500,
    delay:0,
    easing:'linear',
    name:'animate'
}

export const animateFactory = (duration: string|number, delay: string|number, easing: string, name: string): AnimationEntryMetadata => {
    duration = duration || defaults.duration
    delay = delay || defaults.delay
    easing = easing || defaults.easing
    name = name || defaults.name

    let timing: string = [
        typeof(duration) === 'number' ? `${duration}ms` : duration,
        typeof(delay) === 'number' ? `${delay}ms` : delay,
        easing
    ].join(' ');

    return trigger(name, [
        ...fade(timing),
        ...bounce(timing),
        ...rotate(timing),
        ...slide(timing),
        ...zoom(timing)
    ]);
};

export const factory = (options={}) => {
    return animateFactory(options.duration, options.delay, options.easing, options.name);
};


